        <img :src="require('../assets/example1.png')" >
      </el-row>
      <el-row type="flex" class="row-bg" justify="center">
        <h2>Location Control </h2>
      </el-row>
      <el-row type="flex" class="row-bg" justify="center">
        <el-col >
          <div style="font-size:24px;">
            Unlike global conditional input such as segmentation maps, depth maps, sketches and grayscale images, character generation requires more attention to a specific local area of the image, since the latent feature distribution of character pixels varies dramatically from that of natural image pixels. To prevent model learning from collapsing, a fine-grained location area control is innovatively introduced to decouple the distribution between different regions. Specifically, as shown in Fig 2, a binary mask feature map is generated and concatenated to original image latent feature. During the training phase, the quadrilateral-shaped mask is extracted from OCR detection information. During inference phase, since there is no available reference image, the mask is generated through a mask prediction module during early diffusion stage.
          </div>
        </el-col>
      </el-row>
      <el-row type="flex" class="row-bg" justify="center">
        <h2>Glyph Control </h2>
      </el-row>
      <el-row type="flex" class="row-bg" justify="center">
        <el-col >
          <div style="font-size:24px;">
            A significant aspect of generating images embedded in Chinese characters is the complexity of the glyph, which results from the orderly distribution of multiple strokes in a flat box. Typically, the number of strokes in common Chinese characters ranges from 0 to 20, and even a slight shift in the stroke position can affect the recognition of Chinese characters. Therefore, to generate Chinese characters accurately, we consider embedding Chinese character images in the model in two ways. The first way is to concatenate the image to the noisy latent, similar to the mask image. The second way is to combine global control with projection and add them to the timestep embedding, along with text embeddings. To achieve this goal, a plausible approach is to concatenate the font image embeddings with text embeddings. However, we find that this leads to bad results empirically, where not only the Chinese characters are not generated, but also semantic information is lost. As a result, we introduce a model framework called MergeNet to learn how to integrate the two embeddings' information.
          </div>
        </el-col>
      </el-row>
        
      </el-card>
    </el-col>
  </el-row>
