        document.body.scrollTop;
      // // 滚动条滚动的距离
      // let scrollStep = scrollTop - this.oldScrollTop;
      // console.log("header 滚动距离 ", scrollTop);
      // // 更新——滚动前，滚动条距文档顶部的距离
      // this.oldScrollTop = scrollTop;

      //变量windowHeight是可视区的高度
      // let windowHeight =
      //   document.documentElement.clientHeight || document.body.clientHeight;
      //变量scrollHeight是滚动条的总高度
      let scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;
        // console.log(scrollTop/scrollHeight);
      if(scrollTop/scrollHeight > 0.70){
        this.getData()
      }
      //滚动条到底部的条件
      // if (scrollTop + windowHeight == scrollHeight) {
      //   //你想做的事情
        
      //   this.getData()
      // }
      // if (scrollStep < 0) {
      //   console.log("header 滚动条向上滚动了！");
      // } else {
      //   console.log("header  滚动条向下滚动了！");
      // }
      // // 判断是否到了最顶部
      // if (scrollTop <= 0) {
      //   console.log("header 到了最顶部")
      // } 
    },
    getData(){
      this.loading = true;
      this.imgsArrDisplay = this.imgsArrDisplay.concat(this.imgsArr.slice(this.sliceIndex-50,this.sliceIndex));
        this.sliceIndex +=50
        console.log(this.imgsArrDisplay.length)
      this.loading = false;
    },
    loadmore() {
      this.loading = true;
      setTimeout(() => {
        this.imgsArrDisplay = this.imgsArrDisplay.concat(this.imgsArr.slice(this.sliceIndex-50,this.sliceIndex));
        this.sliceIndex +=50
        this.loading = false;
        console.log(this.imgsArrDisplay.length)
      }, 1000);
    },
    scrollFn(){
      // this.$nextTick(() => {
      //   console.log("滚了")
      // this.componentKey += 1;
      //       })

    },
    imgClick(index){
      console.log(index)
      this.$viewer.full()
      this.viewer.view(index)
    },
    inited (viewer) {
        this.$viewer = viewer
        this.viewer = viewer
        
      },
    selectChange(val){
      console.log(val)
    },
    onSubmit(){
      this.loading = true
      axios({
            method: "post",
            url: "/api/rongzhi-topic-image/topic-image/analysis/text/list",
            data: JSON.stringify({'tab': this.selectTab,'query':this.inputQuery,'topk':2000}),
            headers: {
                'Content-Type': 'application/json;charset=UTF-8'
                  }
            }).then(resp => {
              console.log(resp)
          if (resp.data.code == 200) {
            this.imgsArr = []
            this.swiperImageList1 = []
            this.imgsArrDisplay = []
            this.sliceIndex = 50
            for(var item of resp.data.data){
                  if(item['id'] != -1) {
                    this.imgsArr.push({"url":item['url'],"info":item['distance'],"id":item['id']})
                    this.swiperImageList1.push(item['url'])
                  }

                }
                this.getData()
            this.$nextTick(() => {
              
              // this.loadmore()
              })

          }
          else {
            this.$message({
            showClose: true,
            message: resp.data.msg,
            type: 'error'
            });
            this.loading = false
          }
          this.loading = false
            })
    },
    },
}
</script>

